<template>
    <v-toolbar class="header">
        <v-toolbar-title>
            <h1 class="headerTitle mr-10">Test Execution Reports</h1>
        </v-toolbar-title>
        <div v-if="isLoaded" class="header-logo d-flex">
            <img :src="imagePath" alt="header-logo" />
        </div>
        <v-spacer></v-spacer>
        <div class="accion-logo">
            <img src="../assets/accionlabs-logo.87ad6b31.png" alt="accion-logo" />
        </div>
    </v-toolbar>
</template>

<script>
export default {
    name: 'HeaderComponent',
    props: ['isLoaded'],
    data() {
        return {
            imagePath: ""
        }
    },
    watch: {
        isLoaded: function (newVal) {
            if (newVal) {
                this.imagePath = window.info.customerLogo;
            }
        }
    }
}
</script>

<style>
.header {
    position: sticky !important;
    top: 0;
    z-index: 9;
}

.headerTitle {
    font-size: 18px;
}

.header-logo img {
    height: 30px;
}

.accion-logo img {
    height: 40px;
}
</style>