<template>
    <div>
        <v-row>
            <v-col lg="2" md="2" sm="2" xs="2">
                <v-card class="pieChartCard">
                    <canvas id="pie-chart"></canvas>
                </v-card>
            </v-col>
            <v-col lg="10" md="10" sm="10" xs="10">
                <v-card class="filterDropdownsCard">
                    <v-card-text>
                        <v-row>
                            <!-- <v-col lg="2" md="6" sm="6" xs="12"> -->
                            <v-col cols="4">
                                <v-select v-model="selectedStatusCodes" :items="statusCodes" label="Status"
                                    @change="changeStatusCodeHandler" item-text="key" item-value="value" color="black"
                                    item-color="black" multiple chips dense>
                                    <template v-slot:prepend-item>
                                        <v-list-item ripple @mousedown.prevent @click="toggleStatusSelectAll">
                                            <v-list-item-action>
                                                <!-- <v-icon :class="selectedStatusCodes.length > 0 ? 'primary--text' : ''"> -->
                                                <v-icon color="black">
                                                    {{ selectAllIcon(selectedStatusCodes, statusCodes) }}
                                                </v-icon>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    Select All
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </template>
                                    <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index < 2" :class="(item.value >= 200 && item.value <= 299)
                                            ? 'seriesPassed'
                                            : (item.value >= 400 && item.value <= 600)
                                                ? 'seriesFailed'
                                                : ''">
                                            <span>{{ item.key }}</span>
                                        </v-chip>
                                        <span v-if="index === 2" class="grey--text text-caption">
                                            (+{{ selectedStatusCodes.length - 2 }} others)
                                        </span>
                                    </template>
                                </v-select>
                            </v-col>
                            <!-- <v-col lg="2" md="6" sm="6" xs="12"> -->
                            <v-col cols="4">
                                <v-select v-model="selectedSpecLink" :items="specsLinks" label="Specs"
                                    @change="changeSpecLinkHandler" item-text="key" item-value="value" color="black"
                                    item-color="black" multiple chips dense>
                                    <template v-slot:prepend-item>
                                        <v-list-item ripple @mousedown.prevent @click="toggleSpecsLinkSelectAll">
                                            <v-list-item-action>
                                                <v-icon color="black">
                                                    {{ selectAllIcon(selectedSpecLink, specsLinks) }}
                                                </v-icon>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    Select All
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </template>
                                    <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index < 2">
                                            <span>{{ item.key }}</span>
                                        </v-chip>
                                        <span v-if="index === 2" class="grey--text text-caption">
                                            (+{{ selectedSpecLink.length - 2 }} others)
                                        </span>
                                    </template>
                                </v-select>
                            </v-col>
                            <!-- <v-col lg="2" md="6" sm="6" xs="12"> -->
                            <v-col cols="4">
                                <v-select v-model="selectedResults" :items="results" label="Result"
                                    @change="changeResultHandler" item-text="key" item-value="value" color="black"
                                    item-color="black" multiple chips dense>
                                    <template v-slot:prepend-item>
                                        <v-list-item ripple @mousedown.prevent @click="toggleResultSelectAll">
                                            <v-list-item-action>
                                                <v-icon color="black">
                                                    {{ selectAllIcon(selectedResults, results) }}
                                                </v-icon>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    Select All
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </template>
                                    <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index < 2" :class="item.value">
                                            <span>{{ item.key }}</span>
                                        </v-chip>
                                        <span v-if="index === 2" class="grey--text text-caption">
                                            (+{{ selectedResults.length - 2 }} others)
                                        </span>
                                    </template>
                                </v-select>
                            </v-col>
                            <!-- <v-col lg="2" md="6" sm="6" xs="12"> -->
                            <v-col cols="4">
                                <v-select v-model="selectedMethods" :items="methods" label="Methods"
                                    @change="changeMethodHandler" item-text="key" item-value="value" color="black"
                                    item-color="black" multiple chips dense>
                                    <template v-slot:prepend-item>
                                        <v-list-item ripple @mousedown.prevent @click="toggleMethodSelectAll">
                                            <v-list-item-action>
                                                <v-icon color="black">
                                                    {{ selectAllIcon(selectedMethods, methods) }}
                                                </v-icon>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    Select All
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </template>
                                    <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index < 2" :class="item.value">
                                            <span>{{ item.key }}</span>
                                        </v-chip>
                                        <span v-if="index === 2" class="grey--text text-caption">
                                            (+{{ selectedMethods.length - 2 }} others)
                                        </span>
                                    </template>
                                </v-select>
                            </v-col>

                            <v-col cols="4">
                                <v-select v-model="selectedResTypes" :items="responseContentTypes"
                                    label="Response Content Type" @change="changeResponseType" item-text="key"
                                    item-value="value" color="black" item-color="black" multiple chips dense>
                                    <template v-slot:prepend-item>
                                        <v-list-item ripple @mousedown.prevent @click="toggleResTypeSelectAll">
                                            <v-list-item-action>
                                                <v-icon color="black">
                                                    {{ selectAllIcon(selectedResTypes, responseContentTypes) }}
                                                </v-icon>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    Select All
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </template>
                                    <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index < 2">
                                            <span>{{ item.key }}</span>
                                        </v-chip>
                                        <span v-if="index === 2" class="grey--text text-caption">
                                            (+{{ selectedResTypes.length - 2 }} others)
                                        </span>
                                    </template>
                                </v-select>
                            </v-col>
                            <v-col cols="4">
                                <v-select v-model="selectedReqTypes" :items="requestContentTypes"
                                    label="Request Content Type" @change="changeRequestType" item-text="key"
                                    item-value="value" color="black" item-color="black" multiple chips dense>
                                    <template v-slot:prepend-item>
                                        <v-list-item ripple @mousedown.prevent @click="toggleReqTypeSelectAll">
                                            <v-list-item-action>
                                                <v-icon color="black">
                                                    {{ selectAllIcon(selectedReqTypes, requestContentTypes) }}
                                                </v-icon>
                                            </v-list-item-action>
                                            <v-list-item-content>
                                                <v-list-item-title>
                                                    Select All
                                                </v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </template>
                                    <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index < 2">
                                            <span>{{ item.key }}</span>
                                        </v-chip>
                                        <span v-if="index === 2" class="grey--text text-caption">
                                            (+{{ selectedReqTypes.length - 2 }} others)
                                        </span>
                                    </template>
                                </v-select>
                            </v-col>
                        </v-row>
                        <v-row>
                            <div class="filterActionBtns">
                                <!-- <v-col lg="3" md="6" sm="6" xs="12" class="mt-2"> -->
                                <div class="mr-5">
                                    <v-btn @click="selectAllHandler" elevation="8" color="primary">
                                        Select All
                                    </v-btn>
                                </div>
                                <div class="mr-5">
                                    <v-btn @click="clearAllHandler" elevation="8" color="error">
                                        Clear All
                                    </v-btn>
                                </div>
                            </div>
                        </v-row>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
        <div v-if="cards.length === 0">
            <p class="text-center font-italic mt-10">{{ infoText }}</p>
        </div>

        <v-row class="cards-wrapper mt-5">
            <InfoCard :cardData="card" v-for="( card, index ) in  cards " :key="index" />
        </v-row>
    </div>
</template>

<script>
import InfoCard from './InfoCard.vue'
import chartConfig from '../config/chart'
import Chart from 'chart.js'

export default {
    name: 'HomePage',
    components: {
        InfoCard
    },
    data() {
        return {
            specsLinks: [],
            results: [{ key: 'Passed', value: 'passed' }, { key: 'Failed', value: 'failed' }, { key: 'Skipped', value: 'skipped' }, { key: 'Pending', value: 'pending' }],

            // statusCodes: [{ key: '200', value: '200' }, { key: '404', value: '404' }, { key: '500', value: '500' }],
            statusCodes: [],
            methods: [],
            responseContentTypes: [],
            requestContentTypes: [],

            selectedStatusCodes: [],
            selectedSpecLink: [],
            selectedResults: ['passed', 'failed', 'skipped', 'pending'],
            selectedMethods: [],
            selectedResTypes: [],
            selectedReqTypes: [],

            allTests: [],
            cards: [],
            chartInstance: null,
            infoText: 'Choose a filter from the dropdown menu to display the corresponding data.',
            dataFileNotFound: false,
        }
    },
    mounted() {
        this.createAllTestsArray();
        // this.loadJSONFile();
    },
    computed: {
        // icon(selectedList, optionsList) {
        //     if (this.selectedSpecLink.length === this.specsLinks.length) {
        //         return 'mdi-checkbox-marked';
        //     }
        //     if (this.selectedSpecLink.length > 0 && (this.selectedSpecLink.length !== this.specsLinks.length)) {
        //         return 'mdi-minus-box';
        //     }
        //     return 'mdi-checkbox-blank-outline';
        // },
    },
    methods: {
        // loadJSONFile() {
        //     const self = this;
        //     let script = document.createElement("script");
        //     script.type = "text/javascript";
        //     script.addEventListener("load", function () {
        //         if (window.mochawesome) {
        //             self.createAllTestsArray(window.mochawesome);
        //         } else {
        //             self.infoText = "No data to display."
        //         }
        //     });

        //     script.addEventListener("error", function () {
        //         console.log('file not found !!!');
        //         self.dataFileNotFound = true;
        //     });
        //     script.src = './mochawesome.js';
        //     document.head.appendChild(script);
        // },
        selectAllIcon(selectedList, optionsList) {
            if (selectedList.length === optionsList.length) {
                return 'mdi-checkbox-marked';
            }
            if (selectedList.length > 0 && (selectedList.length !== optionsList.length)) {
                return 'mdi-minus-box';
            }
            return 'mdi-checkbox-blank-outline';
        },
        createAllTestsArray() {
            const statuses = [];
            const methods = [];
            const responseTypes = [];
            const requestTypes = [];
            window.mochawesome.results.forEach(file => {
                const parts = file.file.split("\\");
                const fileName = parts[parts.length - 1];
                const result = fileName.replace(".cy.js", "");
                this.specsLinks.push({ key: result, value: file.file });

                file.suites.forEach(suite => {
                    suite.tests.forEach(test => {
                        const splitTitle = test.title.split(" ");
                        const removeFirstFourEle = splitTitle.slice(4);

                        if (!statuses.includes(splitTitle[0])) {
                            statuses.push(splitTitle[0])
                        }
                        if (!methods.includes(splitTitle[1])) {
                            methods.push(splitTitle[1])
                        }
                        if (splitTitle[2] && !responseTypes.includes(splitTitle[2])) {
                            responseTypes.push(splitTitle[2])
                        }
                        if (splitTitle[3] && !requestTypes.includes(splitTitle[3])) {
                            requestTypes.push(splitTitle[3])
                        }

                        const match = test.code.match(/cy\.fixture\('([^']+)'\)/);
                        const fixtureName = match ? match[1] + '.json' : null;

                        this.allTests.push({
                            ...test,
                            statusCode: splitTitle[0],
                            method: splitTitle[1],
                            responseContentType: splitTitle[2],
                            requestContentType: splitTitle[3],
                            onlyTitle: removeFirstFourEle.join(" "),
                            parentSuite: suite,
                            parentFile: file,
                            fixtureFile: fixtureName,
                        });
                    });
                });
            });

            this.statusCodes = statuses.map(ele => ({ key: ele, value: ele }));
            this.methods = methods.map(ele => ({ key: ele, value: ele }));
            this.responseContentTypes = responseTypes.map(ele => ({ key: ele, value: ele }));
            this.requestContentTypes = requestTypes.map(ele => ({ key: ele, value: ele }));

            if (this.allTests.length === 0) {
                this.infoText = "No data to display.";
            }
            this.selectAllHandler(null, true);

        },
        drawPieChart() {
            if (this.chartInstance instanceof Chart) {
                this.chartInstance.destroy();
            }

            const pieChartConfig = JSON.parse(JSON.stringify(chartConfig.pieChart))

            let passedCount = 0;
            let failedCount = 0;
            let skippedCount = 0;
            let pendingCount = 0;
            this.cards.forEach(card => {
                if (card.pass) passedCount++;
                if (card.fail) failedCount++;
                if (card.skipped) skippedCount++;
                if (card.pending) pendingCount++;
            });

            pieChartConfig.data.labels = [`Passed (${passedCount})`, `Failed (${failedCount})`, `Skipped (${skippedCount})`, `Pending (${pendingCount})`];

            pieChartConfig.options.tooltips = {
                callbacks: {
                    label: function (tooltipItem, data) {
                        return data.labels[tooltipItem.index]
                    }
                }
            };

            pieChartConfig.data.datasets[0].data = [passedCount, failedCount, skippedCount, pendingCount];
            const element = document.getElementById('pie-chart');
            this.chartInstance = new Chart(element, pieChartConfig);

        },
        // createSpecsLinks() {
        //     return mockawesome.results.map(ele => {
        //         const parts = ele.file.split("\\");
        //         const fileName = parts[parts.length - 1];
        //         const result = fileName.replace(".cy.js", "");

        //         return { key: result, value: ele.file }
        //     });
        // },

        toggleStatusSelectAll() {
            this.$nextTick(() => {
                if (this.selectedStatusCodes.length === this.statusCodes.length) {
                    this.selectedStatusCodes = [];
                } else {
                    this.selectedStatusCodes = this.statusCodes.map(e => e.value);
                }
                this.filterTestArray();
            });
        },
        toggleSpecsLinkSelectAll() {
            this.$nextTick(() => {
                if (this.selectedSpecLink.length === this.specsLinks.length) {
                    this.selectedSpecLink = [];
                } else {
                    this.selectedSpecLink = this.specsLinks.map(e => e.value);
                }
                this.filterTestArray();
            });
        },
        toggleResultSelectAll() {
            this.$nextTick(() => {
                if (this.selectedResults.length === this.results.length) {
                    this.selectedResults = [];
                } else {
                    this.selectedResults = this.results.map(e => e.value);
                }
                this.filterTestArray();
            });
        },
        toggleMethodSelectAll() {
            this.$nextTick(() => {
                if (this.selectedMethods.length === this.methods.length) {
                    this.selectedMethods = [];
                } else {
                    this.selectedMethods = this.methods.map(e => e.value);
                }
                this.filterTestArray();
            });
        },
        toggleResTypeSelectAll() {
            this.$nextTick(() => {
                if (this.selectedResTypes.length === this.responseContentTypes.length) {
                    this.selectedResTypes = [];
                } else {
                    this.selectedResTypes = this.responseContentTypes.map(e => e.value);
                }
                this.filterTestArray();
            });
        },
        toggleReqTypeSelectAll() {
            this.$nextTick(() => {
                if (this.selectedReqTypes.length === this.requestContentTypes.length) {
                    this.selectedReqTypes = [];
                } else {
                    this.selectedReqTypes = this.requestContentTypes.map(e => e.value);
                }
                this.filterTestArray();
            });
        },
        changeStatusCodeHandler() {
            this.filterTestArray();
        },
        changeSpecLinkHandler() {
            this.filterTestArray();
        },
        changeResultHandler() {
            this.filterTestArray();
        },
        changeMethodHandler() {
            this.filterTestArray();
        },
        changeResponseType() {
            this.filterTestArray();
        },
        changeRequestType() {
            this.filterTestArray();
        },

        filterTestArray() {
            if (this.selectedStatusCodes.length === 0 &&
                this.selectedSpecLink.length === 0 &&
                this.selectedResults.length === 0 &&
                this.selectedMethods.length === 0 &&
                this.selectedResTypes.length === 0 &&
                this.selectedReqTypes.length === 0) {
                this.cards = [];
                this.drawPieChart();
                return;
            }

            const res = this.allTests.filter(item => (
                (this.selectedStatusCodes.length === 0 || this.selectedStatusCodes.includes(item.statusCode)) &&
                (this.selectedSpecLink.length === 0 || this.selectedSpecLink.includes(item.parentFile.file)) &&
                (this.selectedResults.length === 0 || this.selectedResults.includes(item.state)) &&
                (this.selectedMethods.length === 0 || this.selectedMethods.includes(item.method)) &&
                (this.selectedResTypes.length === 0 || this.selectedResTypes.includes(item.responseContentType)) &&
                (this.selectedReqTypes.length === 0 || this.selectedReqTypes.includes(item.requestContentType))
            ));

            this.cards = JSON.parse(JSON.stringify(res));
            this.drawPieChart();
        },

        selectAllHandler(e, onPageLoad) {
            this.selectedStatusCodes = this.statusCodes.map(e => e.value);
            this.selectedSpecLink = this.specsLinks.map(e => e.value);
            this.selectedResults = this.results.map(e => e.value);
            this.selectedMethods = this.methods.map(e => e.value);
            if (!onPageLoad) {
                this.selectedResTypes = this.responseContentTypes.map(e => e.value);
                this.selectedReqTypes = this.requestContentTypes.map(e => e.value);
            }

            this.filterTestArray();
        },
        clearAllHandler() {
            this.selectedStatusCodes = [];
            this.selectedSpecLink = [];
            this.selectedResults = [];
            this.selectedMethods = [];
            this.selectedResTypes = [];
            this.selectedReqTypes = [];

            this.filterTestArray();
        }
    }
}
</script>

<style>
.pieChartCard {
    height: 100%;
    padding: 5px;
}

.filterDropdownsCard {
    height: 100%;
    padding: 5px;
}

.filterActionBtns {
    margin: 5px;
    display: flex;
}

.v-label {
    font-weight: bold;
    color: black !important;
}

.v-list-item__title {
    font-size: 16px !important;
    color: black;
    line-height: 1.5rem !important;
}

/* .v-list-item--active .v-list-item__title {
    color: #1976d2 !important;
} */

.v-chip {
    margin: 1px 1px !important;
    padding: 10px !important;
}

.cards-wrapper {
    word-wrap: break-word;
    /* justify-content: space-between; */
}
</style>