<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->

    <v-app id="app">
      <HeaderComponent :isLoaded="isFileLoaded" />
      <v-main>
        <v-container fluid>
          <div v-if="isFileLoaded">
            <div v-if="dataFileNotFound">
              <p class="text-center font-italic mt-10">No data to display.</p>
            </div>
            <HomePage v-else />
          </div>
        </v-container>
      </v-main>
      <FooterComponent />
    </v-app>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import HeaderComponent from './components/Header.vue'
import FooterComponent from './components/Footer.vue'
import HomePage from './components/HomePage.vue'

export default {
  name: 'App',
  components: {
    // HelloWorld,
    HeaderComponent,
    FooterComponent,
    HomePage,
  },
  data() {
    return {
      isFileLoaded: false,
      dataFileNotFound: false,
    }
  },
  mounted() {
    this.loadDataJSFile();
  },
  methods: {
    loadDataJSFile() {
      const self = this;
      let script = document.createElement("script");
      script.type = "text/javascript";
      script.addEventListener("load", function () {
        self.isFileLoaded = true;
        if (!window.mochawesome) {
          self.dataFileNotFound = true;
        }
      });

      script.addEventListener("error", function () {
        console.log('file not found !!!');
        self.isFileLoaded = true;
        self.dataFileNotFound = true;
      });
      script.src = './mochawesome.js';
      document.head.appendChild(script);
    }
  }
}
</script>

<style>
html {
  /* overflow-y: hidden !important; */
  color: #455a64;
}

* {
  text-transform: none !important;
  font-family: "Roboto", sans-serif;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  /* color: #2c3e50;
  margin-top: 60px; */
}

.v-main {
  background-color: #fff !important;
  padding: 20px !important;
  height: 100%;
}

.col {
  padding: 8px !important;
}

/* CSS for Scroll Bar*/
div::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}

div::-webkit-scrollbar-corner {
  background: rgba(0, 0, 0, 0);
}

div::-webkit-scrollbar-track {
  background: #e4e4ef;
  /* border-left: 1px solid #2c2c2c; */
  border-radius: 7px;
}

div::-webkit-scrollbar-thumb {
  background: #726d6d;
  border-radius: 7px;
}

div::-webkit-scrollbar-thumb:hover {
  background: gray;
}
</style>
